.PasswordBar {
    position: absolute;
    bottom: 255px;
    width: 280px;
    height: 5px;
    border-radius: 15px;
    background-color: #e5f1ef;
@media screen and (min-width: 768px) {
    width:410px;
    bottom: 342px;
    }
        @media screen and (min-width: 1280px) {
            bottom: 348px;
        }
}

.bar {
    transition: width 500ms ease;
}
.weak {
    transition: width 500ms ease;
    position: absolute;
    top: 0;
    left: 0;
    height: 5px;
    width: 10%;
    background-color: #24CCA7;
    border-radius: 15px;
}

.medium {
    transition: width 500ms ease;
    height: 5px;
    width: 40%;
    background-color: #24CCA7;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 15px;
}

.Fine {
    transition: width 500ms ease;
    height: 5px;
    width: 70%;
    background-color: #24CCA7;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 15px;
}

.strong {
    transition: width 500ms ease;
    height: 5px;
    width: 100%;
    background-color: #24CCA7;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 15px;
}